#chart {
  background: #040d14;
  background: var(--main-bg);
}

#yAxis > .domain {
  stroke-width: 0;
}

#yAxis > .tick line {
  stroke-opacity: 0.5;
  stroke-dasharray: 2;
  stroke: #00c805;
  stroke: var(--green-full-opacity);
}

#yAxis > .tick text {
  transform: translate(-880px, -8px);
  fill: #00c805;
  fill: var(--green-full-opacity);
  font-size: 12px;
  font-family: "Work Sans", sans-serif;
}

#xAxis > .domain,
#xAxis > .tick line {
  stroke: #00c805;
  stroke: var(--green-full-opacity);
}

#xAxis > .tick text {
  fill: #00c805;
  fill: var(--green-full-opacity);
  font-size: 12px;
  font-family: "Work Sans", sans-serif;
}

#path {
  stroke: #00c805;
  stroke: var(--green-full-opacity);
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
}

